* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Jost;
}
.app {
  display: grid;
  width: 100%;
  height: 100vh;
  place-items: center;
}
.div {
  background-color: rgb(8, 127, 247);
  color: white;
  padding: 1rem;
  max-width: 500px;
  box-shadow: 1px 1px 10px 1px rgba(8, 127, 247, 0.466);
  border-radius: 5px;
}
.div h1 {
  font-size: 2.5rem;
  font-family: 600;
}
.navbar {
  background-color: rgb(8, 127, 247);
  color: white;
  padding: 1rem;
  max-width: 500px;
  box-shadow: 1px 1px 10px 1px rgba(8, 127, 247, 0.466);
}
.item {
  color: white;
  text-decoration: none;
  padding-inline: 0.5rem;
}
.item:hover {
  opacity: 0.8;
}
.item:active {
  opacity: 0.5;
}
.on {
  border-inline: 1px solid #fff;
}
a[tabindex]:focus {
  color: blue;
  outline: none;
}
.fof {
  background-color: rgb(219, 0, 0);
  color: white;
  padding: 1rem;
  max-width: 500px;
  box-shadow: 1px 1px 10px 1px rgba(219, 0, 0, 0.466);
  border-radius: 5px;
}
